	module.exports = {"data":{"postsJson":{"title":"Jesus Entered Jerusalem","questionsTitle":"ВОПРОСЫ, ВОПРОСЫ, ВОПРОСЫ","display":"block","bookName":"От Луки ","ref":"19:28-21:38","html":"<p>   <strong>19</strong><u>28</u>Сказав это, Он пошел далее, восходя в Иерусалим.<br />   <u>29</u>И когда приблизился к Виффагии и Вифании, к горе, называемой Елеонскою, послал двух учеников Своих,<br />   <u>30</u>сказав: </p><h1>JESUS</h1><h2>пойдите в противолежащее селение; войдя в него, найдете молодого осла привязанного, на которого никто из людей никогда не садился; отвязав его, приведите;<br />   <u>31</u>и если кто спросит вас: зачем отвязываете? скажите ему так: он надобен Господу.</h2><p> <u>32</u>Посланные пошли и нашли, как Он сказал им.<br />   <u>33</u>Когда же они отвязывали молодого осла, хозяева его сказали им: </p><h1>THE OWNERS OF THE COLT</h1><h4>зачем отвязываете осленка?</h4><p> <u>34</u>Они отвечали: </p><h1>TWO DISCIPLES</h1><h3>он надобен Господу.</h3><p> <u>35</u>И привели его к Иисусу, и, накинув одежды свои на осленка, посадили на него Иисуса.<br />   <u>36</u>И, когда Он ехал, постилали одежды свои по дороге.<br />   <u>37</u>А когда Он приблизился к спуску с горы Елеонской, все множество учеников начало в радости велегласно славить Бога за все чудеса, какие видели они,<br />   <u>38</u>говоря: </p><h1>THE CROWD</h1><h4>благословен Царь, грядущий во имя Господне! мир на небесах и слава в вышних!</h4><p> <u>39</u>И некоторые фарисеи из среды народа сказали Ему: </p><h1>THE PHARISEES</h1><h4>Учитель! запрети ученикам Твоим.</h4><p> <u>40</u>Но Он сказал им в ответ: </p><h1>JESUS</h1><h2>сказываю вам, что если они умолкнут, то камни возопиют.</h2><p> <u>41</u>И когда приблизился к городу, то, смотря на него, заплакал о нем<br />   <u>42</u>и сказал: </p><h1>JESUS</h1><h2>о, если бы и ты хотя в сей твой день узнал, что служит к миру твоему! Но это сокрыто ныне от глаз твоих,<br />   <u>43</u>ибо придут на тебя дни, когда враги твои обложат тебя окопами и окружат тебя, и стеснят тебя отовсюду,<br />   <u>44</u>и разорят тебя, и побьют детей твоих в тебе, и не оставят в тебе камня на камне за то, что ты не узнал времени посещения твоего.</h2><p> <u>45</u>И, войдя в храм, начал выгонять продающих в нем и покупающих,<br />   <u>46</u>говоря им: </p><h1>JESUS</h1><h2>написано: дом Мой есть дом молитвы, а вы сделали его вертепом разбойников.</h2><p> <u>47</u>И учил каждый день в храме. Первосвященники же и книжники и старейшины народа искали погубить Его,<br />   <u>48</u>и не находили, что бы сделать с Ним; потому что весь народ неотступно слушал Его.<br />   <strong>20</strong><u>1</u>В один из тех дней, когда Он учил народ в храме и благовествовал, приступили первосвященники и книжники со старейшинами,<br />   <u>2</u>и сказали Ему: </p><h1>JEWISH LEADERS</h1><h4>скажи нам, какою властью Ты это делаешь, или кто дал Тебе власть сию?</h4><p> <u>3</u>Он сказал им в ответ: </p><h1>JESUS</h1><h2>спрошу и Я вас об одном, и скажите Мне:<br />   <u>4</u>крещение Иоанново с небес было, или от человеков?</h2><p> <u>5</u>Они же, рассуждая между собою, говорили: </p><h1>JEWISH LEADERS</h1><h4>если скажем: с небес, то скажет: почему же вы не поверили ему?<br />   <u>6</u>а если скажем: от человеков, то весь народ побьет нас камнями, ибо он уверен, что Иоанн есть пророк.</h4><p> <u>7</u>И отвечали: </p><h1>JEWISH LEADERS</h1><h4>не знаем откуда.</h4><p> <u>8</u>Иисус сказал им: </p><h1>JESUS</h1><h2>и Я не скажу вам, какою властью это делаю.</h2><p> <u>9</u>И начал Он говорить к народу притчу сию: </p><h1>JESUS</h1><h2>один человек насадил виноградник и отдал его виноградарям, и отлучился на долгое время;<br />   <u>10</u>и в свое время послал к виноградарям раба, чтобы они дали ему плодов из виноградника; но виноградари, прибив его, отослали ни с чем.<br />   <u>11</u>Еще послал другого раба; но они и этого, прибив и обругав, отослали ни с чем.<br />   <u>12</u>И еще послал третьего; но они и того, изранив, выгнали.<br />   <u>13</u>Тогда сказал господин виноградника: что мне делать? Пошлю сына моего возлюбленного; может быть, увидев его, постыдятся.<br />   <u>14</u>Но виноградари, увидев его, рассуждали между собою, говоря: это наследник; пойдем, убьем его, и наследство его будет наше.<br />   <u>15</u>И, выведя его вон из виноградника, убили. Что же сделает с ними господин виноградника?<br />   <u>16</u>Придет и погубит виноградарей тех, и отдаст виноградник другим. </h2><p>Слышавшие же это сказали: </p><h1>THE CROWD</h1><h4>да не будет!</h4><p> <u>17</u>Но Он, взглянув на них, сказал: </p><h1>JESUS</h1><h2>что значит сие написанное: камень, который отвергли строители, тот самый сделался главою угла?<br />   <u>18</u>Всякий, кто упадет на тот камень, разобьется, а на кого он упадет, того раздавит.</h2><p> <u>19</u>И искали в это время первосвященники и книжники, чтобы наложить на Него руки, но побоялись народа, ибо поняли, что о них сказал Он эту притчу.<br />   <u>20</u>И, наблюдая за Ним, подослали лукавых людей, которые, притворившись благочестивыми, уловили бы Его в каком-либо слове, чтобы предать Его начальству и власти правителя.<br />   <u>21</u>И они спросили Его: </p><h1>THE PHARISEES &amp; THE HERODIANS</h1><h4>Учитель! мы знаем, что Ты правдиво говоришь и учишь и не смотришь на лице, но истинно пути Божию учишь;<br />   <u>22</u>позволительно ли нам давать подать кесарю, или нет?</h4><p> <u>23</u>Он же, уразумев лукавство их, сказал им: </p><h1>JESUS</h1><h2>что вы Меня искушаете?<br />   <u>24</u>Покажите Мне динарий: чье на нем изображение и надпись? </h2><p>Они отвечали: </p><h1>THE PHARISEES &amp; THE HERODIANS</h1><h4>Кесаревы.</h4><p> <u>25</u>Он сказал им: </p><h1>JESUS</h1><h2>итак, отдавайте кесарево кесарю, а Божие Богу.</h2><p> <u>26</u>И не могли уловить Его в слове перед народом, и, удивившись ответу Его, замолчали.<br />   <u>27</u>Тогда пришли некоторые из саддукеев, отвергающих воскресение, и спросили Его:</p><h1>THE SADDUCEES</h1><h4> <u>28</u>Учитель! Моисей написал нам, что если у кого умрет брат, имевший жену, и умрет бездетным, то брат его должен взять его жену и восставить семя брату своему.<br />   <u>29</u>Было семь братьев, первый, взяв жену, умер бездетным;<br />   <u>30</u>взял ту жену второй, и тот умер бездетным;<br />   <u>31</u>взял ее третий; также и все семеро, и умерли, не оставив детей;<br />   <u>32</u>после всех умерла и жена;<br />   <u>33</u>итак, в воскресение которого из них будет она женою, ибо семеро имели ее женою?</h4><p> <u>34</u>Иисус сказал им в ответ: </p><h1>JESUS</h1><h2>чада века сего женятся и выходят замуж;<br />   <u>35</u>а сподобившиеся достигнуть того века и воскресения из мертвых ни женятся, ни замуж не выходят,<br />   <u>36</u>и умереть уже не могут, ибо они равны Ангелам и суть сыны Божии, будучи сынами воскресения.<br />   <u>37</u>А что мертвые воскреснут, и Моисей показал при купине, когда назвал Господа Богом Авраама и Богом Исаака и Богом Иакова.<br />   <u>38</u>Бог же не есть [Бог] мертвых, но живых, ибо у Него все живы.</h2><p> <u>39</u>На это некоторые из книжников сказали: </p><h1>SOME RELIGIOUS LEADERS</h1><h4>Учитель! Ты хорошо сказал.</h4><p> <u>40</u>И уже не смели спрашивать Его ни о чем. Он же сказал им:</p><h1>JESUS</h1><h2> <u>41</u>как говорят, что Христос есть Сын Давидов,<br />   <u>42</u>а сам Давид говорит в книге псалмов: сказал Господь Господу моему: седи одесную Меня,<br />   <u>43</u>доколе положу врагов Твоих в подножие ног Твоих?<br />   <u>44</u>Итак, Давид Господом называет Его; как же Он Сын ему?</h2><p> <u>45</u>И когда слушал весь народ, Он сказал ученикам Своим:</p><h1>JESUS</h1><h2> <u>46</u>остерегайтесь книжников, которые любят ходить в длинных одеждах и любят приветствия в народных собраниях, председания в синагогах и предвозлежания на пиршествах,<br />   <u>47</u>которые поедают домы вдов и лицемерно долго молятся; они примут тем большее осуждение.</h2><p>   <strong>21</strong><u>1</u>Взглянув же, Он увидел богатых, клавших дары свои в сокровищницу;<br />   <u>2</u>увидел также и бедную вдову, положившую туда две лепты,<br />   <u>3</u>и сказал: </p><h1>JESUS</h1><h2>истинно говорю вам, что эта бедная вдова больше всех положила;<br />   <u>4</u>ибо все те от избытка своего положили в дар Богу, а она от скудости своей положила все пропитание свое, какое имела.</h2><p> <u>5</u>И когда некоторые говорили о храме, что он украшен дорогими камнями и вкладами, Он сказал:</p><h1>JESUS</h1><h2> <u>6</u>придут дни, в которые из того, что вы здесь видите, не останется камня на камне; все будет разрушено.</h2><p> <u>7</u>И спросили Его: </p><h1>FOUR DISCIPLES</h1><h3>Учитель! когда же это будет? и какой признак, когда это должно произойти?</h3><p> <u>8</u>Он сказал: </p><h1>JESUS</h1><h2>берегитесь, чтобы вас не ввели в заблуждение, ибо многие придут под именем Моим, говоря, что это Я; и это время близко: не ходите вслед их.<br />   <u>9</u>Когда же услышите о войнах и смятениях, не ужасайтесь, ибо этому надлежит быть прежде; но не тотчас конец.</h2><p> <u>10</u>Тогда сказал им: </p><h1>JESUS</h1><h2>восстанет народ на народ, и царство на царство;<br />   <u>11</u>будут большие землетрясения по местам, и глады, и моры, и ужасные явления, и великие знамения с неба.<br />   <u>12</u>Прежде же всего того возложат на вас руки и будут гнать [вас], предавая в синагоги и в темницы, и поведут пред царей и правителей за имя Мое;<br />   <u>13</u>будет же это вам для свидетельства.<br />   <u>14</u>Итак положите себе на сердце не обдумывать заранее, что отвечать,<br />   <u>15</u>ибо Я дам вам уста и премудрость, которой не возмогут противоречить ни противостоять все, противящиеся вам.<br />   <u>16</u>Преданы также будете и родителями, и братьями, и родственниками, и друзьями, и некоторых из вас умертвят;<br />   <u>17</u>и будете ненавидимы всеми за имя Мое,<br />   <u>18</u>но и волос с головы вашей не пропадет, --<br />   <u>19</u>терпением вашим спасайте души ваши.<br />   <u>20</u>Когда же увидите Иерусалим, окруженный войсками, тогда знайте, что приблизилось запустение его:<br />   <u>21</u>тогда находящиеся в Иудее да бегут в горы; и кто в городе, выходи из него; и кто в окрестностях, не входи в него,<br />   <u>22</u>потому что это дни отмщения, да исполнится все написанное.<br />   <u>23</u>Горе же беременным и питающим сосцами в те дни; ибо великое будет бедствие на земле и гнев на народ сей:<br />   <u>24</u>и падут от острия меча, и отведутся в плен во все народы; и Иерусалим будет попираем язычниками, доколе не окончатся времена язычников.<br />   <u>25</u>И будут знамения в солнце и луне и звездах, а на земле уныние народов и недоумение; и море восшумит и возмутится;<br />   <u>26</u>люди будут издыхать от страха и ожидания [бедствий], грядущих на вселенную, ибо силы небесные поколеблются,<br />   <u>27</u>и тогда увидят Сына Человеческого, грядущего на облаке с силою и славою великою.<br />   <u>28</u>Когда же начнет это сбываться, тогда восклонитесь и поднимите головы ваши, потому что приближается избавление ваше.</h2><p> <u>29</u>И сказал им притчу: </p><h1>JESUS</h1><h2>посмотрите на смоковницу и на все деревья:<br />   <u>30</u>когда они уже распускаются, то, видя это, знаете сами, что уже близко лето.<br />   <u>31</u>Так, и когда вы увидите то сбывающимся, знайте, что близко Царствие Божие.<br />   <u>32</u>Истинно говорю вам: не прейдет род сей, как все это будет;<br />   <u>33</u>небо и земля прейдут, но слова Мои не прейдут.<br />   <u>34</u>Смотрите же за собою, чтобы сердца ваши не отягчались объядением и пьянством и заботами житейскими, и чтобы день тот не постиг вас внезапно,<br />   <u>35</u>ибо он, как сеть, найдет на всех живущих по всему лицу земному;<br />   <u>36</u>итак бодрствуйте на всякое время и молитесь, да сподобитесь избежать всех сих будущих [бедствий] и предстать пред Сына Человеческого.</h2><p> <u>37</u>Днем Он учил в храме, а ночи, выходя, проводил на горе, называемой Елеонскою.<br />   <u>38</u>И весь народ с утра приходил к Нему в храм слушать Его.</p>","path":"/336","Question1":"Перечислите все вопросы, поднятые в тексте. Кто их делает? Кому они сделаны?","Question2":"Если там вопросы который ближе к вам? Почему?","Question3":"Какие вопросы вы хотели бы задать автору данного текста?","Question4":"Какие последствия имеют эти вопросы из текста для вашей жизни?","prevPath":"/335","nextPath":"/337"}},"pathContext":{}}

